import { useParams } from "react-router-dom";
import { soalMatematika } from "./soalMatematika";
export default function Testpage() {
  let { slug } = useParams();
  
  return (
    <div className="py-10 pl-5 lg:pl-48 pr-5 lg:pr-32 container  h-full">
      <div className="lg:flex items-center justify-between border-b-2 pb-5">
        <p> Materi Tes : {slug}</p>
        <p>Waktu Tersisa :</p>
      </div>
      <form  className="mt-5">
        {soalMatematika.map((soal, index) => (
            <div className="mb-5 border p-10 bg-green-100 rounded-lg" key="index">
            <p className="mb-5">{soal.soal}</p>
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-3">
             <div> <input className="rounded-full " type="checkbox" />{" "}
              <label htmlFor="">{soal.a}</label></div>
              <div> <input className="rounded-full" type="checkbox" />{" "}
              <label htmlFor="">{soal.a}</label></div>
              <div> <input className="rounded-full" type="checkbox" />{" "}
              <label htmlFor="">{soal.a}</label></div>
              <div> <input className="rounded-full" type="checkbox" />{" "}
              <label htmlFor="">{soal.a}</label></div>
              <div> <input className="rounded-full" type="checkbox" />{" "}
              <label htmlFor="">{soal.a}</label></div>
            </div>
            </div>
        ) )}
        <button type="submit" className="bg-green-500 w-full text-white py-5 uppercase font-bold">Selesai</button>
      </form>
    </div>
  );
}
